






























import { Component, Vue, Watch } from 'vue-property-decorator';
import HomeMenu from '@/application/Home/View/HomeMenu.vue';
import Config from '@/application/Home/View/navConfig';
import CommonGameSelector from './CommonGameSelector.vue';

@Component({
  components: {
    HomeMenu,
    CommonGameSelector
  }
})
export default class ShotNavigation extends Vue{
  private list = Config;
  private showPicker?: boolean = false;
  private currentPageText?: string = '';
  private gamePickerShow?: boolean = false;
  private onClickLeft() {
    this.$router.push('/home');
  }
  private togglePicker() {
    this.showPicker = !this.showPicker;
  }
  @Watch('$route')
  private routerChange(value: any) {
    this.changeLinkAction(value.name || '');
  }
  private mounted() {
    this.changeLinkAction(this.$route.name || '');
  }
  private changeLinkAction(linkName?: string) {
    const match: {[T: string]: any}[] = this.list.filter((item: any) => item.link === linkName);
    if (match.length > 0) {
      const item: {[T: string]: string} = match[0];
      this.currentPageText = item.text;
      this.showPicker = false;
    }
  }
  private gamesChange(value: any) {
    this.gamePickerShow = false;
    console.log(value);
  }
}
