















import { Component, Vue } from 'vue-property-decorator';
import Config from './navConfig';
import { Toast } from 'vant';

@Component
export default class HomeMenu extends Vue {
  public menuItems: any[] = Config;
  private getMenuImage(img: string) {
    return require(`@/resources/images/home/${img}`);
  }
  private goPage(url: string) {
    const currName = this.$route.name;
    if (currName != url) {
      this.$router.push(`/${url}`);
    } else {
      Toast.fail('当前页面');
    }
  }
}
